import React, { Component } from 'react'
// import classes from './MyMap.module.scss'
import { Map, SearchControl, Polyline, Placemark, Button } from 'react-yandex-maps';

export default class MyMap extends Component {
    async getCoords(request) {
        return await this.props.ymaps.geocode(request).then(res => res.geoObjects.properties._data.metaDataProperty.GeocoderResponseMetaData.Point.coordinates.reverse())
    }
    async onSearchSubmit(e) {
        const new_coords = await this.getCoords(e.originalEvent.request)
        if (!this.props.activePoint) this.props.onAdd({
            coordinates: new_coords
        }) 
    }
    placemarkRefs = []
    async onDrag(e, index) {
        const new_coords = e._sourceEvent.originalEvent.target.geometry._coordinates
        this.props.onDrag(index, new_coords)
    }
    addMarkerInCenter() {
        this.props.onAdd({
            coordinates: this.mapRef.getCenter()
        })
    }
    onDragActive(e) {
        const new_coords = e._sourceEvent.originalEvent.target.geometry._coordinates
        this.props.onAdd({
            coordinates: new_coords
        })
    }
    render() {
        return (
            <Map    defaultState={{ center: [55.75, 37.57], zoom: 9 }} 
                    className={this.props.className} 
                    instanceRef={ref => this.mapRef = ref}
                    onClick={() => this.mapRef.balloon.close()}>
                <SearchControl  options={{ float: 'left', noPlacemark: true }} 
                                onSubmit={e => this.onSearchSubmit(e)} instanceRef={ref => this.searchRef = ref}/>
                <Polyline   geometry={this.props.markerList.map(el => el.coordinates)}
                            options={{
                                strokeColor: '#000',
                                strokeWidth: 4,
                                strokeOpacity: 0.5
                            }}
                />
                <Button
                    options={{ maxWidth: 200, selectOnClick: false }}
                    data={{ content: 'Add marker in map center!' }}
                    onClick={this.addMarkerInCenter.bind(this)}
                />
                { 
                    this.props.markerList.map((el, index) => (
                    <Placemark  geometry={el.coordinates} 
                                options={{ draggable: true }}
                                properties={{
                                    balloonContentBody: `<h2>${el.name}</h2>`
                                }}
                                instanceRef={ref => this.placemarkRefs[index] = ref}
                                onClick={ () => this.placemarkRefs[index].balloon.open() }
                                key={index} 
                                onDrag={ (e) => this.onDrag(e, index) }/>))
                }
                {
                    this.props.activePoint && 
                    <Placemark  geometry={this.props.activePoint.coordinates} 
                                options={{ draggable: true }} 
                                properties={{
                                    
                                }}
                                onDrag={ (e) => this.onDragActive(e)}/>
                }
            </Map>                     
        )
    }
}