import Main from './Main'
import { connect } from 'react-redux'
import {    addMarker, 
            deleteMarker,
            updateMarkerList,
            updateMarker,
            fixMarker } from '../../store/actions/map_actions';

const mapStateToProps = state => ({
    activePoint: state.map.active_point,
    pointsData: state.map.points_data
})

const mapDisptachToProps = dispatch => ({
    addMarker: data => dispatch(addMarker(data)),
    deleteMarker: id => dispatch(deleteMarker(id)),
    updateMarkerList: data => dispatch(updateMarkerList(data)),
    updateMarker: (id, data) => dispatch(updateMarker(id, data)),
    fixMarker: (name) => dispatch(fixMarker(name))
})

export default connect(mapStateToProps, mapDisptachToProps)(Main)