import {    ADD_MARKER, 
            DELETE_MARKER, 
            UPADATE_MARKER_LIST,
            UPDATE_MARKER, 
            FIX_MARKER } from 'const/map_actions_const' 

const initialState = {
    active_point: null,
    points_data: [
        {
            name: 'First test',
            coordinates: [55.8, 37.5]
        },
        {
            name: 'Second test',
            coordinates: [55.8, 37.4]
        },
        {
            name: 'Third test',
            coordinates: [55.7, 37.5]
        }
    ]
}

export default (state = initialState, action) => {
    switch (action.type) {
        case ADD_MARKER: return {
            ...state,
            active_point: action.payload
        }
        case DELETE_MARKER: return {
            ...state,
            points_data: state.points_data.filter((elem, index) => index !== action.payload)
        }
        case UPDATE_MARKER: return {
            ...state,
            points_data: state.points_data.map((point, index) => index === action.payload.id ? { ...point, ...action.payload.data } : point )
        }
        case UPADATE_MARKER_LIST: return {
            ...state,
            points_data: action.payload
        }
        case FIX_MARKER: return {
            ...state,
            active_point: null,
            points_data: [ ...state.points_data, { ...state.active_point, name: action.payload } ]
        }
        default: return state
    }
}