import {    ADD_MARKER, 
            DELETE_MARKER, 
            UPADATE_MARKER_LIST, 
            UPDATE_MARKER,
            FIX_MARKER } from 'const/map_actions_const' 
/**
 * @description add marker to list
 * @param Object data - marker data
 */
export const addMarker = data => ({
    type: ADD_MARKER,
    payload: data
})
/**
 * @description delete marker from list
 * @param Number id - marker id
 */
export const deleteMarker = id => ({
    type: DELETE_MARKER,
    payload: id
})
/**
 * @description update marker list
 * @param Array data - marker list
 */
export const updateMarkerList = data => ({
    type: UPADATE_MARKER_LIST,
    payload: data
})
/**
 * @description update marker info
 * @param Number id - marker id
 * @param Array data - marker data
 */
export const updateMarker = (id, data) => ({
    type: UPDATE_MARKER,
    payload: {
        id, 
        data
    }
})
/**
 * @description fix marker and add it to marker list
 * @param String name - marker name
 */
export const fixMarker = (name) => ({
    type: FIX_MARKER,
    payload: name
})