import React from 'react'
import classes from './MarkerManager.module.scss'
import Input from 'components/Input'
import MarkerField from 'components/MarkerField'
import { DragDropContext } from 'react-beautiful-dnd';
import DroppableWrapper from 'components/DroppableWrapper'
import DraggableWrapper from 'components/DraggableWrapper'

export default props => (
    <DragDropContext onDragEnd={props.onDragEnd}>
        <div className={[classes.main, props.className].join(' ')}>
            <div className={classes.form}>
                <Input  value={props.value} 
                        onChange={ props.onChange } 
                        placeholder='Enter marker name' 
                        className={ classes.markerInput}
                        onEnter={ props.onNameSubmit } />

                <button disabled={ !props.activePoint } 
                        onClick={ props.onFix }>Enter</button>
            </div>
            <DroppableWrapper droppableId='markers' className={classes.fields}>
                {
                    props.markerList.map((elem , index) => 
                        <DraggableWrapper   draggableId={elem.name+index} 
                                            index={index} 
                                            key={index}
                                            className={ classes.markerField }>
                            <MarkerField    key={ index } 
                                            id={ index }
                                            data={ elem }
                                            onDelete={ props.onDelete } />
                        </DraggableWrapper>)
                }
            </DroppableWrapper>
        </div>
    </DragDropContext>
)