import React from 'react'
import classes from './MarkerField.module.scss'
import DeleteImage from 'assets/images/delete-button.png'

export default props => (
    <div className={ [classes.main, props.className].join(' ') } >
        <div className={ classes.info }>
            <span className={ classes.id }>
                { props.id }.
            </span>
            <div className={ classes.markerInfo }>
                <h3>{ props.data.name}</h3>
                <div className={ classes.coordinates }>
                    <h4><span>Latitude: </span>{ props.data.coordinates[0] }</h4>
                    <h4><span>Longtitude: </span>{ props.data.coordinates[1] }</h4>
                </div>
            </div>
        </div>
        <div className={ classes.actions }>
            <img src={ DeleteImage } alt="asds" onClick={ () => props.onDelete(props.id) }/>
        </div>
    </div>
)