import React, { Component } from 'react'
import classes from './Main.module.scss'
import MyMap from 'components/MyMap';
import MarkerManager from 'components/MarkerManager'

export default class Main extends Component {
    constructor(props) {
        super(props)
        this.state = {
            activePointName: ''
        }
    }
    async onDrag(index, new_coords) {
        this.props.updateMarker(index, { coordinates: new_coords })
    }
    async onMarkerDelete(id) {
        this.props.deleteMarker(id)
    }
    onAdd(data) {
        this.props.addMarker(data)
    }
    onDragEnd(e) {
        if (e.destination) {
            const from = e.source.index
            const to = e.destination.index
            let new_list = new Array(...this.props.pointsData)
            new_list.splice(from, 1)
            new_list.splice(to, 0, this.props.pointsData[from])
            console.log(new_list)
            this.props.updateMarkerList(new_list)
        }
    }
    async onChange(e) {
        await this.setState({
            activePointName: e.target.value
        })
    }
    async onFix() {
        if (this.props.activePoint) this.props.fixMarker(this.state.activePointName || 'Sample name')
        await this.setState({
            activePointName: ''
        })
    }
    render() {
        return (
            <div className={ classes.main }>
                <MarkerManager  className={ classes.markerManager } 
                                markerList={ this.props.pointsData }
                                activePoint={ this.props.activePoint }
                                onDelete={ this.onMarkerDelete.bind(this) }
                                onDragEnd={ this.onDragEnd.bind(this) }
                                onNameSubmit={ this.onFix.bind(this) }
                                onChange={ this.onChange.bind(this) }
                                onFix={ this.onFix.bind(this) }
                                value={ this.state.activePointName }
                                />
                <MyMap  className={ classes.map } 
                        markerList={ this.props.pointsData } 
                        onDrag={ this.onDrag.bind(this) } 
                       
                        onAdd={ this.onAdd.bind(this) }
                        activePoint={ this.props.activePoint } />
            </div>
        )
    }
}